  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value);
      this.deliveryWindowPerformanceService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "ALL" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "All" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "all" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "All" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "all" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "aLL" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.mcSearchtext='ALL';
            }
            else{
            this.templateError = "No Data for" + "   " + "[" + this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.deliveryWindowPerformanceForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "ALL" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "All" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "all" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "All" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "all" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "aLL" || this.deliveryWindowPerformanceForm.get("mcCustomerGroup")?.value == "aLl")
            {
              this.deliveryWindowPerformanceForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
            }
        }
      })
    }
  }

  
