import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoggedOutComponent } from './shared/layout/logged-out/logged-out.component';
import { PageNotFoundComponent } from './shared/layout/page-not-found/page-not-found.component';
import { UnAuthorizedAccessComponent } from './shared/layout/un-authorized-access/un-authorized-access.component';
import { AuthGuard } from './shared/services/auth.guard';
import { PendingPickupNfsComponent } from './components/pending-pickup-nfs/pending-pickup-nfs.component';
import { HomeComponent } from './components/home/home.component';
import { PendingConnectionFirstMileComponent } from './components/pending-connection-first-mile/pending-connection-first-mile.component';
import { PendingConnectionAllLegsComponent } from './components/pending-connection-all-legs/pending-connection-all-legs.component';
import { DelvMonitorComponent } from './components/delv-monitor/delv-monitor.component';
import { ExpectedToArriveComponent } from './components/expected-to-arrive/expected-to-arrive.component';
import { ActionableRtoComponent } from './components/actionable-rto/actionable-rto.component';
import { UndeliveredUDComponent } from './components/undelivered-ud/undelivered-ud.component';
import { VendorwisePickupsComponent } from './components/vendorwise-pickups/vendorwise-pickups.component';
import { EddTrackerDatewiseComponent } from './components/edd-tracker-datewise/edd-tracker-datewise.component';
import { DeliveryWindowPerformanceComponent } from './components/delivery-window-performance/delivery-window-performance.component';
import { BddDeliveryWindowPerformanceComponent } from './components/bdd-delivery-window-performance/bdd-delivery-window-performance.component';
import { CovidMonitorsDelvLocationComponent } from './components/covid-monitors-delv-location/covid-monitors-delv-location.component';
import { ExpctdToConnectGnmsNMComponent } from './components/expctd-to-connect-gnms-nm/expctd-to-connect-gnms-nm.component';
import { ExpctdToArriveGnmsNMComponent } from './components/expctd-to-arrive-gnms-nm/expctd-to-arrive-gnms-nm.component';
import { LiveTrackerStatusScanComponent } from './components/live-tracker-status-scan/live-tracker-status-scan.component';
import { PdaRspSlaComponent } from './components/pda-rsp-sla/pda-rsp-sla.component';
import { KamCustomerReportComponent } from './components/kam-customer-report/kam-customer-report.component';
import { LoadPlanningExpctdToArriveComponent } from './components/load-planning-expctd-to-arrive/load-planning-expctd-to-arrive.component';
import { EarlyWarningSystemAverageComponent } from './components/early-warning-system-average/early-warning-system-average.component';
import { EarlyWarningSystemCapacityComponent } from './components/early-warning-system-capacity/early-warning-system-capacity.component';

const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full'},
  { path: 'un-authorized-access', component: UnAuthorizedAccessComponent },
  { path: 'logged-out', component: LoggedOutComponent, canActivate:[AuthGuard],},
  { path: 'pending-pickup-nfs', component: PendingPickupNfsComponent, canActivate:[AuthGuard],},
  { path: 'pending-connection-first-mile', component: PendingConnectionFirstMileComponent, canActivate:[AuthGuard],},
  { path: 'pending-connection-all-legs', component: PendingConnectionAllLegsComponent, canActivate:[AuthGuard],},
  { path: 'delv-monitor', component: DelvMonitorComponent, canActivate:[AuthGuard],},
  { path: 'expected-to-arrive', component: ExpectedToArriveComponent, canActivate:[AuthGuard],},
  { path: 'actionable-rto', component: ActionableRtoComponent, canActivate:[AuthGuard],},
  { path: 'undeliveredUD', component: UndeliveredUDComponent, canActivate:[AuthGuard],},
  { path: 'vendorwise-pickups', component: VendorwisePickupsComponent, canActivate:[AuthGuard],},
  { path: 'edd-tracker-datewise', component: EddTrackerDatewiseComponent, canActivate:[AuthGuard],},
  { path: 'delivery-window-performance', component: DeliveryWindowPerformanceComponent, canActivate:[AuthGuard],},
  { path: 'bdd-delivery-window-performance', component: BddDeliveryWindowPerformanceComponent, canActivate:[AuthGuard],},
  { path: 'covid-monitors-delv-location', component: CovidMonitorsDelvLocationComponent, canActivate:[AuthGuard],},
  { path: 'expctd-to-connect-gnms-NM', component: ExpctdToConnectGnmsNMComponent, canActivate:[AuthGuard],},
  { path: 'expctd-to-arrive-gnms-NM', component: ExpctdToArriveGnmsNMComponent, canActivate:[AuthGuard],},
  { path: 'live-tracker-status-scan', component: LiveTrackerStatusScanComponent, canActivate:[AuthGuard],},
  { path: 'pda-rsp-sla', component: PdaRspSlaComponent, canActivate:[AuthGuard],},
  { path: 'kam-customer-report', component: KamCustomerReportComponent, canActivate:[AuthGuard],},  
  { path: 'load-planning-expctd-to-arrive', component: LoadPlanningExpctdToArriveComponent, canActivate:[AuthGuard],},
  { path: 'early-warning-system-capacity', component: EarlyWarningSystemCapacityComponent, canActivate:[AuthGuard],},
  { path: 'early-warning-system-average', component: EarlyWarningSystemAverageComponent, canActivate:[AuthGuard],},
  { path: 'dashboard', component: HomeComponent, },
  { path: "**", component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
