  onFetechCustGroup() {
    this.mcCustomerGroup = '';
    this.mcSearchtext = '';
    if (this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == null) {
      this.templateError = "Please enter Cust Group"
    }
    else {
      this.templateError = '';
      let queryParams = new HttpParams();
      queryParams = queryParams.append('CCOMPGRP', this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value);
      this.covidMonitorsDelvLocationService.getCustGroupDetails(queryParams).subscribe((res: any) => {
        if (res.result) {

          if (res.result.custGroupList == null || res.result.custGroupList === 0 || res.result.custGroupList.length === 0) {
            if (this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "ALL" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "All" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "all" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "All" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "all" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "aLL" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "aLl") {
              this.mcSearchtext = 'ALL';
            }
            else {
              this.templateError = "No Data for" + "   " + "[" + this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value + "]" + "   " + "this Cust Group";
            }
          }
          else {
            this.covidMonitorsDelvLocationForm.patchValue({
              mcCustomerGroup: res.result.custGroupList[0].CCOMPGRP,
              mcSearchtext: res.result.custGroupList[0].CCOMPNAME
            });
          }
          if (this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "ALL" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "All" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "all" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "All" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "all" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "aLL" || this.covidMonitorsDelvLocationForm.get("mcCustomerGroup")?.value == "aLl") {
            this.covidMonitorsDelvLocationForm.patchValue({
              mcCustomerGroup: "ALL",
              mcSearchtext: "ALL"
            });
          }
        }
      })
    }
  }

  
